<template>
  <v-container>
    <div v-if="currentMember" class="edit-form py-3">
      <!-- <p class="headline">Edit User</p> -->

      <v-row>
        <v-col class="text-center" cols="12" md="4">
          <v-row class="pa-5">
            <h2>EDIT USER</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/addMember"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >ADD USER</v-btn
          >
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/members"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >MEMBER LIST</v-btn
          >
        </v-col>
      </v-row>

      <v-form ref="form" lazy-validation>
        <v-text-field
          v-model="currentMember.username"
          :rules="[(v) => !!v || 'Name is required']"
          label="Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="currentMember.email"
          :rules="[(v) => !!v || 'Email is required']"
          label="Email"
          required
        ></v-text-field>

        <v-text-field
          v-model="currentMember.password"
          :rules="[(v) => !!v || 'Password is required']"
          label="Password"
          type="password"
          required
        ></v-text-field>

        <v-select
          v-model="currentMember.branch"
          :items="branch"
          item-text="name"
          item-value="roleCode"
          :menu-props="{ maxHeight: '400' }"
          label="BRANCH"
          hint="Set the user branch"
          persistent-hint
        ></v-select>

        <v-select
          v-model="currentMember.dept"
          :items="dept"
          item-text="name"
          item-value="name"
          :menu-props="{ maxHeight: '400' }"
          label="DEPARTMENT"
          hint="Set the user department"
          persistent-hint
        ></v-select>

        <label><strong>Status:</strong></label>
        {{ currentMember.active ? "Active" : "Inactive" }}

        <v-divider class="my-5"></v-divider>

        <v-btn
          v-if="currentMember.active"
          @click="updateActive(false)"
          color="primary"
          small
          disabled
          class="mr-2"
        >
          Inactive
        </v-btn>

        <v-btn
          v-else
          @click="updateActive(true)"
          color="primary"
          small
          disabled
          class="mr-2"
        >
          Active
        </v-btn>

        <v-btn color="error" small class="mr-2" @click="deleteMember">
          Delete
        </v-btn>

        <v-btn color="success" small @click="updateMember"> Update </v-btn>
      </v-form>

      <p class="mt-3">{{ message }}</p>
    </div>

    <div v-else>
      <p>Please click on a User...</p>
    </div>
  </v-container>
</template>

<script>
import MemberDataService from "../../services/MemberDataService";
import BranchDataService from "../../services/BranchDataService";
import DeptDataService from "../../services/DeptDataService";

export default {
  name: "member",
  data() {
    return {
      currentMember: null,
      message: "",
      branch: [],
      dept: [],
    };
  },
  methods: {
    getMember(id) {
      MemberDataService.get(id)
        .then((response) => {
          this.currentMember = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    updateActive(status) {
      var data = {
        // id: this.currentMember.id,
        // username: this.currentMember.username,
        // email: this.currentMember.email,
        active: status,
      };

      MemberDataService.update(this.currentMember.id, data)
        .then((response) => {
          this.currentMember.active = status;
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    updateMember() {
      MemberDataService.update(this.currentMember.id, this.currentMember)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "members" });
          // this.message = "The user was updated successfully!";
        })
        .catch((e) => {
          console.log(e);
        });
    },

    deleteMember() {
      MemberDataService.delete(this.currentMember.id)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "members" });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveBranch() {
      BranchDataService.getAll()
        .then((response) => {
          this.branch = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveDept() {
      DeptDataService.getAll()
        .then((response) => {
          this.dept = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.message = "";
    this.getMember(this.$route.params.id);
    this.retrieveBranch();
    this.retrieveDept();
  },
};
</script>
